.sidepanel {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  height: 100vh;
  width: 0vw;
  background-color: #4baca3cc;
  overflow-x: hidden;
}

.hide {
  display: none;
}

.show {
  padding: 40px;
  width: 20vw;
  padding-top: 9vh;
}

.transition {
  transition: 1s;
}

.sidepanel a {
  color: rgb(255, 255, 255);
  font-family: Futura, Helvetica Neue, sans-serif;
  font-size: 10px;
  font-weight: 300;
  display: block;
  text-decoration: none;
}

.sidepanel a:hover {
  color: #474747;
}

.sidepanel .closebtn {
  position: absolute;
  top: 0;
  right: 4vw;
  font-size: 36px;
}

.sidepanel .leftarrow {
  position: absolute;
  top: 5vh;
  left: 7vw;
  font-size: 40px;
}

.sidepanel .rightarrow {
  position: absolute;
  top: 5vh;
  right: 7vw;
  font-size: 40px;
}

.sidepanel p {
  color: rgb(255, 255, 255);
  font-family: Futura, Helvetica Neue, sans-serif;
  font-size: 18px;
  font-weight: 300;
  display: block;
  transition: 0s;
}

.circuit-button {
  background-color: white;
  border-radius: 4px;
  padding: 2px 16px 2px 16px;
  font-weight: 200;
  font-family: Roboto Regular, Helvetica Neue, sans-serif;
  font-size: 14px;
  color: #209798;
  border: none;
  position: absolute;
  top: 4vh;
  left: 2vh;
  line-height: 30px;
  border-radius: 10px;
  border: 1px solid #dae0de;
}

@media only screen and (max-width : 770px) {
  .show {
    padding-top: 70px;
    width: 60vw;
  }

  .sidepanel p {
    font-size: 22px;
  }

  .app {
    width: 100vw;
    height: 90vh;
  }

  .sidepanel .leftarrow {
    left: 25vw;
    font-size: 40px;
  }

  .sidepanel .rightarrow {

    right: 25vw;
    font-size: 40px;
  }

}